function initTooltip(place) {
    const tooltip = document.createElement('div');
    tooltip.classList.add('flex', 'flex_column', 'flex_nowrap', 'cart-tooltip');
    tooltip.innerHTML = `
        <div class="flex flex_nowrap items_center">
            <object class="cart-tooltip__icon" data="img/svg/cart_added.svg" type="image/svg+xml"></object>
            <span class="cart-tooltip__title">Товар добавлен в корзину</span>
        </div>
        <span class="cart-tooltip__product-title">Название товара есть над чем задуматься: многие известные</span>
        <a class="text_secondary cart-tooltip__link" href="/cart.html">Смотреть</a>
    `
    place.append(tooltip);
    setTimeout(() => {
        place.removeChild(tooltip);
    }, 4000);
}
