const headerDefault = document.querySelector('.header_default');
const headerScrolled = document.querySelector('.header_scrolled');
const catalogDrop = document.querySelector('.catalog-drop');
const headerFirst = document.querySelector('.header-first__wrapper');
const headerSecond = document.querySelector('.header-second__wrapper');
const headerThird = document.querySelector('.header-third__wrapper');
const menuButton = headerSecond.querySelector('.header-second__menu-button');
const mobileMenuList = headerSecond.querySelector('.mobile-menu__list');
const menuLinks = mobileMenuList.querySelectorAll('.mobile-menu__list-link');
const catalogButton = headerThird.querySelector('.header-third__catalog-trigger-button');
const catalogButtonScrolled = headerScrolled.querySelector('.sh__catalog-button');
const mobileCatalogNav = headerThird.querySelector('.mobile-header-nav');
const mobileNavItems = headerThird.querySelectorAll('.mobile-header-nav__item');
const mobileCatalogLinks = mobileCatalogNav.querySelectorAll('.mobile-header-nav__link');

document.addEventListener('scroll', () => {
    if (window.pageYOffset > 200 && window.innerWidth > 1024) {
        headerScrolled.classList.remove('hidden-top');
    } else {
        headerScrolled.classList.add('hidden-top');
    }
})
menuButton.addEventListener('click', () => {
    if (menuButton.classList.contains('active')) {
        menuButton.classList.remove('active');
        mobileMenuList.classList.remove('active');
    } else {
        menuButton.classList.add('active');
        mobileMenuList.classList.add('active');
    }
});
menuLinks.forEach(link => {
    link.addEventListener('click', () => {
        menuButton.classList.remove('active');
        mobileMenuList.classList.remove('active');
    });
});
catalogButton.addEventListener('click', () => {
    if (window.width < 1025) {
        if (catalogButton.classList.contains('active')) {
            catalogButton.classList.remove('active');
            mobileCatalogNav.classList.remove('active');
        } else {
            catalogButton.classList.add('active');
            mobileCatalogNav.classList.add('active');
        }
    }
});
catalogButtonScrolled.addEventListener('mouseenter', () => {
    catalogDrop.classList.add('active', 'fixed');
});
catalogButton.addEventListener('mouseenter', () => {
    catalogDrop.classList.add('active');
});
catalogButton.addEventListener('mouseleave', (e) => {
    setTimeout(() => {
        if (!e.relatedTarget.classList.contains('catalog-drop')) {
            catalogDrop.classList.remove('active');
        }
    }, 500)
})
catalogDrop.addEventListener('mouseleave', () => {
    catalogDrop.classList.remove('active', 'fixed');
})
mobileCatalogLinks.forEach(link => {
    const listener = function (e) {
        if (link.nextElementSibling) {
            const nestedList = link.nextElementSibling;
            if (nestedList.classList.contains('active')) {
                nestedList.classList.remove('active');
                mobileNavItems.forEach(link => link.classList.remove('hidden'));
                e.target.classList.remove('expanded');
            } else {
                nestedList.classList.add('active');
                mobileNavItems.forEach(link => link.classList.add('hidden'));
                e.target.parentElement.classList.remove('hidden');
                e.target.classList.add('expanded');
            }
        }
    }
    link.addEventListener('click', listener, false);
})

const inpSearch = () => {
    let inputSearch = document.querySelector('.search-form__label');
    let headerSecondSearchForm = document.querySelector('.header-second__search-form-targ');
    let block = document.querySelector('.block');
    let headerThirdWrapper = document.querySelector('.header-third__wrapper')

    function closeSearchBlock() {
        block.classList.remove('active');
        blockSm.classList.remove('active');
    }

    document.addEventListener('click', (e) => {
        e.preventDefault();

        if (e.target.closest('.header-second__search-form-targ')) {
            inputSearch.classList.add('active');
            headerSecondSearchForm.classList.add('active');
            headerThirdWrapper.classList.add('active');
        }

        if (!e.target.closest('.header-second__search-form-targ')) {
            inputSearch.classList.remove('active');
            headerSecondSearchForm.classList.remove('active');
            headerThirdWrapper.classList.remove('active');

            closeSearchBlock()
        }

        document.addEventListener('keydown', (e) => {
            if (e.code == 'Escape') {
                inputSearch.classList.remove('active');
                headerSecondSearchForm.classList.remove('active');
                headerThirdWrapper.classList.remove('active');

                closeSearchBlock()
            }
        })

    })
}

inpSearch();

const inpSearch2 = () => {
    let shSearchFormTarg = document.querySelector('.sh__search-form-targ');
    let blockSh = document.querySelector('.block-sh');

    function closeSearchBlock() {
        blockSh.classList.remove('active');
    }

    document.addEventListener('click', (e) => {
        e.preventDefault();

        if (e.target.closest('.sh__search-form-targ')) {
            shSearchFormTarg.classList.add('active');
            blockSh.classList.add('active');
        }

        if (!e.target.closest('.sh__search-form-targ')) {
            shSearchFormTarg.classList.remove('active');
            blockSh.classList.remove('active');

            closeSearchBlock()
        }

        document.addEventListener('keydown', (e) => {
            if (e.code == 'Escape') {
                inputSearch.classList.remove('active');
                shSearchFormTarg.classList.remove('active');
                blockSh.classList.remove('active');

                closeSearchBlock()
            }
        })

    })
}

inpSearch2();


let searchButton = document.querySelector('.search-button');
let block = document.querySelector('.block');
let blockSm = document.querySelector('.block-sm');
let headerThirdWrapperT = document.querySelector('.header-third__wrapper-t')

let bb = document.querySelector('.bb')


if (searchButton) {
    searchButton.addEventListener('click', (e) => {
        block.classList.toggle('active');
    })
}

bb.addEventListener('click', (e) => {
    blockSm.classList.toggle('active');
})


